import { NextRequest, NextResponse } from 'next/server';
import Stripe from 'stripe';
import { db, Timestamp, COLLECTIONS, FieldValue } from '@/lib/firebase';
import { logInfo, logError } from '@/utils/logger';

// Lazy initialize Stripe to avoid build-time errors
let stripeInstance: Stripe | null = null;
function getStripe(): Stripe {
  if (!stripeInstance) {
    stripeInstance = new Stripe(process.env.STRIPE_SECRET_KEY!, {
      apiVersion: '2025-10-29.clover',
    });
  }
  return stripeInstance;
}

export const dynamic = 'force-dynamic';

/**
 * POST /api/webhooks/stripe
 * Handle Stripe webhook events
 *
 * Events handled:
 * - payment_intent.succeeded
 * - payment_intent.payment_failed
 * - charge.refunded
 * - customer.subscription.created
 * - customer.subscription.updated
 * - customer.subscription.deleted
 */
export async function POST(request: NextRequest) {
  const startTime = Date.now();

  try {
    const body = await request.text();
    const sig = request.headers.get('stripe-signature');

    if (!sig) {
      return NextResponse.json({ error: 'No signature' }, { status: 400 });
    }

    // Verify webhook signature
    let event: Stripe.Event;
    try {
      event = getStripe().webhooks.constructEvent(
        body,
        sig,
        process.env.STRIPE_WEBHOOK_SECRET!
      );
    } catch (err) {
      logError('Stripe webhook signature verification failed', err);
      return NextResponse.json({ error: 'Invalid signature' }, { status: 400 });
    }

    logInfo('Stripe webhook received', { type: event.type, id: event.id });

    // Handle the event
    switch (event.type) {
      case 'payment_intent.succeeded': {
        const paymentIntent = event.data.object as Stripe.PaymentIntent;
        await handlePaymentSuccess(paymentIntent);
        break;
      }

      case 'payment_intent.payment_failed': {
        const paymentIntent = event.data.object as Stripe.PaymentIntent;
        await handlePaymentFailed(paymentIntent);
        break;
      }

      case 'charge.refunded': {
        const charge = event.data.object as Stripe.Charge;
        await handleRefund(charge);
        break;
      }

      case 'customer.subscription.created':
      case 'customer.subscription.updated': {
        const subscription = event.data.object as Stripe.Subscription;
        await handleSubscriptionChange(subscription);
        break;
      }

      case 'customer.subscription.deleted': {
        const subscription = event.data.object as Stripe.Subscription;
        await handleSubscriptionCancelled(subscription);
        break;
      }

      default:
        logInfo('Unhandled Stripe event type', { type: event.type });
    }

    const duration = Date.now() - startTime;
    logInfo('Stripe webhook processed', { type: event.type, duration });

    return NextResponse.json({ received: true });
  } catch (error) {
    logError('Stripe webhook processing failed', error);
    return NextResponse.json(
      { error: 'Webhook processing failed' },
      { status: 500 }
    );
  }
}

/**
 * Handle successful payment
 */
async function handlePaymentSuccess(paymentIntent: Stripe.PaymentIntent) {
  try {
    const invoiceId = paymentIntent.metadata?.invoiceId;
    const freelancerId = paymentIntent.metadata?.freelancerId;

    if (!invoiceId || !freelancerId) {
      logError('Missing metadata in payment intent', new Error('Missing metadata'), {
        paymentIntentId: paymentIntent.id,
      });
      return;
    }

    const now = Timestamp.now();

    // 1. Update invoice status to paid
    await db.collection(COLLECTIONS.INVOICES).doc(invoiceId).update({
      status: 'paid',
      paidAt: now,
      updatedAt: now,
    });

    // 2. Create transaction record
    const transactionId = `txn_${Date.now()}_${invoiceId.slice(0, 8)}`;
    const amount = paymentIntent.amount / 100; // Convert from cents
    const relayCommission = amount * 0.03; // 3%
    const freelancerNet = amount - relayCommission;

    await db.collection(COLLECTIONS.TRANSACTIONS).doc(transactionId).set({
      transactionId,
      invoiceId,
      freelancerId,
      amount,
      paymentMethod: 'card',
      relayCommission,
      freelancerNet,
      commissionRate: 0.03,
      status: 'completed',
      stripeChargeId: paymentIntent.latest_charge as string,
      transactionDate: now,
      completedAt: now,
      createdAt: now,
    });

    // 3. Update user stats
    await db.collection(COLLECTIONS.USER_STATS).doc(freelancerId).update({
      totalCollected: FieldValue.increment(amount),
      updatedAt: now,
    });

    // 4. Award gamification XP (Â£1 = 1 XP)
    await db.collection(COLLECTIONS.USER_STATS).doc(freelancerId).update({
      gamificationXP: FieldValue.increment(Math.floor(amount)),
    });

    logInfo('Payment success processed', {
      invoiceId,
      amount,
      transactionId,
    });
  } catch (error) {
    logError('Failed to handle payment success', error);
  }
}

/**
 * Handle failed payment
 */
async function handlePaymentFailed(paymentIntent: Stripe.PaymentIntent) {
  try {
    const invoiceId = paymentIntent.metadata?.invoiceId;
    const freelancerId = paymentIntent.metadata?.freelancerId;

    if (!invoiceId) {
      return;
    }

    // Log failed payment attempt
    await db.collection('failed_webhooks').add({
      type: 'payment_failed',
      paymentIntentId: paymentIntent.id,
      invoiceId,
      freelancerId,
      reason: paymentIntent.last_payment_error?.message || 'Unknown error',
      timestamp: Timestamp.now(),
    });

    logInfo('Payment failed logged', {
      invoiceId,
      reason: paymentIntent.last_payment_error?.message,
    });

    // TODO: Send retry email to user
  } catch (error) {
    logError('Failed to handle payment failure', error);
  }
}

/**
 * Handle refund
 */
async function handleRefund(charge: Stripe.Charge) {
  try {
    const paymentIntentId = charge.payment_intent as string;
    if (!paymentIntentId) return;

    // Find original transaction
    const transactionsQuery = await db
      .collection(COLLECTIONS.TRANSACTIONS)
      .where('stripeChargeId', '==', charge.id)
      .limit(1)
      .get();

    if (transactionsQuery.empty) {
      logError('Transaction not found for refund', new Error('Transaction not found'), {
        chargeId: charge.id,
      });
      return;
    }

    const transaction = transactionsQuery.docs[0].data();
    const now = Timestamp.now();

    // Create refund transaction
    const refundId = `refund_${Date.now()}_${transaction.invoiceId.slice(0, 8)}`;
    await db.collection(COLLECTIONS.TRANSACTIONS).doc(refundId).set({
      transactionId: refundId,
      invoiceId: transaction.invoiceId,
      freelancerId: transaction.freelancerId,
      amount: -transaction.amount,
      paymentMethod: 'card',
      relayCommission: -transaction.relayCommission,
      freelancerNet: -transaction.freelancerNet,
      commissionRate: 0.03,
      status: 'completed',
      stripeChargeId: charge.id,
      transactionDate: now,
      completedAt: now,
      createdAt: now,
    });

    // Update user stats
    await db.collection(COLLECTIONS.USER_STATS).doc(transaction.freelancerId).update({
      totalCollected: FieldValue.increment(-transaction.amount),
      updatedAt: now,
    });

    logInfo('Refund processed', {
      refundId,
      amount: transaction.amount,
    });
  } catch (error) {
    logError('Failed to handle refund', error);
  }
}

/**
 * Handle subscription changes
 */
async function handleSubscriptionChange(subscription: Stripe.Subscription) {
  try {
    const customerId = subscription.customer as string;

    // Find user by Stripe customer ID
    const usersQuery = await db
      .collection(COLLECTIONS.USERS)
      .where('stripeCustomerId', '==', customerId)
      .limit(1)
      .get();

    if (usersQuery.empty) {
      logError('User not found for subscription', new Error('User not found'), {
        customerId,
      });
      return;
    }

    const userId = usersQuery.docs[0].id;
    const now = Timestamp.now();

    // Update user subscription
    await db.collection(COLLECTIONS.USERS).doc(userId).update({
      subscriptionTier: subscription.status === 'active' ? 'paid' : 'free',
      subscriptionStartDate: subscription.status === 'active' ? now : null,
      updatedAt: now,
    });

    logInfo('Subscription updated', {
      userId,
      status: subscription.status,
    });
  } catch (error) {
    logError('Failed to handle subscription change', error);
  }
}

/**
 * Handle subscription cancellation
 */
async function handleSubscriptionCancelled(subscription: Stripe.Subscription) {
  try {
    const customerId = subscription.customer as string;

    // Find user by Stripe customer ID
    const usersQuery = await db
      .collection(COLLECTIONS.USERS)
      .where('stripeCustomerId', '==', customerId)
      .limit(1)
      .get();

    if (usersQuery.empty) {
      return;
    }

    const userId = usersQuery.docs[0].id;

    // Downgrade to free tier
    await db.collection(COLLECTIONS.USERS).doc(userId).update({
      subscriptionTier: 'free',
      subscriptionStartDate: null,
      updatedAt: Timestamp.now(),
    });

    logInfo('Subscription cancelled', { userId });
  } catch (error) {
    logError('Failed to handle subscription cancellation', error);
  }
}
